	components: {},
	data() {
		return {
			baseUrl: this.$common.baseUrl,
			langch: {
				chakanbaogao: '查看报告',
				shanchu: '删除',
				shijian: '时间',
				zuijia: '最佳数据',
				youxiao: '有效数据',
				laji: '垃圾数据',
				quantian: '全天数据',
				common: '一般数据'
