			langen: {
				chakanbaogao: 'View report',
				shanchu: 'delete',
				shijian: 'time',
				zuijia: 'optimum',
				youxiao: 'Effective',
				laji: 'garbage',
				quantian: 'All day',
				common: 'common'
			},
			year: new Date().getFullYear(),
			week: ['日', '一', '二', '三', '四', '五', '六'][new Date().getDay()],
			month: new Date().getMonth() + 1,
			day: new Date().getDate(),
			avatar: uni.getStorageSync('userinfo').avatar,
			username: uni.getStorageSync('userinfo').username,
			echartdata: {},
			langtype: '',
			// 当前选中的时间点
			currentIndex: 0,
			// 接收请求的时间点
			timeArray: [],
			option: {
				title: '',
				grid: [{
					top: 50,
					bottom: 30
				}],
				legend: [{
					// orient:'vertical',
					orient: 'horizontal',
					itemGap: 100,
					data: ['L曲线', 'R曲线']
				}],
				dataZoom: [{
					type: 'inside', //这个 dataZoom 组件是 inside 型 dataZoom 组件（能在坐标系内进行拖动，以及用滚轮（或移动触屏上的两指滑动）进行缩放）
					xAxisIndex: 0, //控制x轴
					start: 0,
					end: 100
				}, ],
				xAxis: [{
						type: 'category',
						// axisLabel:{interval: 0},
						boundaryGap: false,
						gridIndex: 0,
						axisTick: {
							inside: false,
							length: 0,
							lineStyle: {

							}
							// show:false
						},
					},
					// {type:'category',position:'bottom',name:'LA',axisPointer:{type:'none'}},
					{
						type: 'category',
						position: 'top',
						name: 'LA',
						boundaryGap: false,
						offset: 30
					}
				],
				yAxis: [{
					type: 'value',
					gridIndex: 0
				}, ],
				dataset: {
					source: [
						// [1,"4", "6"],
						//  [2,"5", "3"],
						//  [3,"8", "9"],
						//  [4,"09", "10"],
						//  [5,"10", "14"],
						//  [6,"11", "12"],

						// ["15:26:02", 3, null],
						// ["15:25:59", 3, null],
						// ["15:25:57", 3, null],
						// ["15:25:54", 3, null],
						// ["15:25:47", 3, null],
						// ["15:25:44", 3, null],
						// ["15:24:26", null, 17],
						// ["15:24:22", null, 18],
						// ["15:24:19", null, 13],
						// ["15:23:59", 17, null],
						// ["15:23:47", null, 7],
						// ["15:23:45", null, 7],
						// ["15:23:36", 26, null],
						// ["15:23:34", 22, null]
					]
				},
				series: [{
						type: 'line',
						color: ['#3F7BE9'],
						name: 'L曲线',
						xAxisIndex: 0,
						yAxisIndex: 0,
						connectNulls:true,
						encode: {
							x: 0,
							y: 1
						}
					},
					{
						type: 'line',
						color: ['#E41620'],
						name: 'R曲线',
						xAxisIndex: 0,
						yAxisIndex: 0,
						connectNulls:true,
						encode: {
							x: 0,
							y: 2
						}
					},
				]
			}
		}
	},
	onShow() {
		this.langtype = uni.getStorageSync('lang') ? uni.getStorageSync('lang') : 'CH',
			this.getTime()
		this._gettime()
		this._searchData()
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		gocontrolpanel() {
			uni.reLaunch({
				url: '/pages/personalCenter/personalCenter'
			})
		},
		// 获取月时间节点
		_gettime() {
			var that = this
			return new Promise((resolve, reject) => {
				that.$common.requests('/api/record/getTimeList').then(res => {
					if (res.data.code == 1) {
						this.timeArray = res.data.data
						resolve(res.data.data)
					}
					reject(res.data.msg)
				})
			})
		},
		// 查询选中的当天数据
		_searchData() {
			var that = this
			this._gettime().then(res => {
				let obj = {
					year: this.year,
					month: this.month,
					hour: that.timeArray[that.currentIndex]
				}
				that.$common.requests('/api/record/getmonthinfo_zhexian', obj).then(res => {
					// console.log(res.data.data.lr)
					that.option.dataset.source = res.data.data.lr
					// console.log(res.data.data)
					that.echartdata = res.data.data
				})
			})

		},
		// 每次切换都发送最亲数据请求
		choose_item(index) {
			this.currentIndex = index
			this._searchData()
		},
		getTime: function() {
			setInterval(() => {
				var aData = new Date();
				this.min = aData.getMinutes();
				this.hour = aData.getHours();
				// sec: new Date().getSeconds(),
				this.sec = aData.getSeconds();
				this.year = aData.getFullYear();
				this.month = aData.getMonth() + 1;
				let weeks = ['日', '一', '二', '三', '四', '五', '六']
				this.week = weeks[aData.getDay()]
				this.day = aData.getDate();
				this.min = aData.getMinutes();
			}, 1000)
		},
	}


}
